const Ofert = () => {
  const ofertsFirst = [
    "peny zakres remontu",
    "renowacja azienki",
    "przerbki hydrauliczne",
    "monta baterii podtynkowych",
    "monta odpywu liniowego",
    "monta kabin prysznicowych",
    "biay monta",
    "glazura, terakota",
    "monta drzwi",
    "monta owietlenia typu LED",
  ];

  const ofertsSecond = [
    "malowanie",
    "szpachlowanie sufitw i cian",
    "suche zabudowy",
    "sufity podwieszane",
    "panele",
    "listwy przypodogowe",
    "terakota, gres",
    "pytki ceramiczne",
    "pytki gipsowe",
    "cega",
    "monta drzwi wewntrznych",
    "monta owietlenia typu LED",
  ];

  const ListItemsFirst = ofertsFirst.map((item) => (
    <li key={item} className="infoBox_list--item">
      {item}
    </li>
  ));

  const ListItemsSecond = ofertsSecond.map((item) => (
    <li key={item} className="infoBox_list--item">
      {item}
    </li>
  ));

  return (
    <section className="Ofert_Page" id="ofert" data-naviitem=".Ofert-NaviItem">
      <h1 className="title_page">OFERTA</h1>
      <div className="Ofert_Page--Content">
        <div className="content_multi-container">
          <div className="multi-container--infoBox">
            <div className="ofert_title">
              <img
                src="/icons/shower.svg"
                alt="houseforofert"
                width="55"
                height="55"
              />
              <h1>azienka</h1>
            </div>
            <hr />
            <ul className="infoBox_list">{ListItemsFirst}</ul>
          </div>
          <div className="multi-container--infoBox">
            <div className="ofert_title">
              <img
                src="/icons/interior.svg"
                alt="houseforofert"
                width="55"
                height="55"
              />
              <h1>Wykoczenia</h1>
            </div>
            <hr />
            <ul className="infoBox_list">{ListItemsSecond}</ul>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Ofert;
